.Backdrop {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(26, 25, 25, 0.7);
}

.BackdropOpen {
    display: block;
}
.BackdropClosed{
